@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
	--primary-color: #d26a47;
	--secondary-color: #254fd8;
}

* {
	font-family: 'Montserrat', sans-serif;
	box-sizing: border-box;
}

.font-family {
	font-family: 'Montserrat', sans-serif;
}

.container,
.container-fluid,
.container-xxl,
.container-xl,
.container-lg,
.container-md,
.container-sm {
	--bs-gutter-x: 1.5rem;
	--bs-gutter-y: 0;
	width: 100%;
	padding-right: calc(var(--bs-gutter-x) * 0.5);
	padding-left: calc(var(--bs-gutter-x) * 0.5);
	margin-right: auto;
	margin-left: auto;
}

@media (min-width: 576px) {

	.container-sm,
	.container {
		max-width: 540px;
	}
}

@media (min-width: 768px) {

	.container-md,
	.container-sm,
	.container {
		max-width: 720px;
	}
}

@media (min-width: 992px) {

	.container-lg,
	.container-md,
	.container-sm,
	.container {
		max-width: 960px;
	}
}

@media (min-width: 1200px) {

	.container-xl,
	.container-lg,
	.container-md,
	.container-sm,
	.container {
		max-width: 1140px;
	}
}

@media (min-width: 1400px) {

	.container-xxl,
	.container-xl,
	.container-lg,
	.container-md,
	.container-sm,
	.container {
		max-width: 1320px;
	}
}

.primary-color {
	background-color: var(--primary-color);
	color: #fff;
}

.secondary-color {
	background-color: var(--secondary-color);
	color: #fff;
}

.text-color {
	color: var(--primary-color);
}

.font-30 {
	font-size: 30px;
}

.center {
	display: flex;
	justify-content: center;
	align-items: center;
}

.end {
	display: flex;
	justify-content: end;
	align-items: center;
}

.effect-wrap .effect {
	position: absolute;
	z-index: -1;
}

.effect-1 {
	width: 30px;
	height: 30px;
	border: 4px solid coral;
	right: 10%;
	bottom: 10%;
	animation: spin 10s linear infinite;
}

.effect-2 {
	left: 3%;
	bottom: 10%;
	width: 40px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	animation: topBounce 3s ease-in-out infinite;
}

.effect-2 div {
	height: 2px;
	width: 2px;
	background-color: coral;
	margin: 0 3px 8px;
}

.effect-3 {
	height: 180px;
	width: 180px;
	border: 20px solid coral;
	border-radius: 50%;
	right: 50%;
	top: -120px;
	animation: rightBounce 3s ease-in-out infinite;
}

.effect-4 {
	border-top: 30px solid transparent;
	border-left: 30px solid coral;
	left: 30%;
	top: 20%;
	animation: spin 15s linear infinite;
}

.effect-4::before {
	content: '';
	border-top: 30px solid transparent;
	border-left: 30px solid coral;
	position: absolute;
	opacity: 0.5;
	left: -35px;
	top: -25px;
}

.effect-5 {
	height: 40px;
	width: 40px;
	right: 10%;
	top: 30%;
	display: flex;
	justify-content: space-between;
	animation: spin 8s linear infinite;
}

.effect-5 div {
	width: 1px;
	background-color: coral;
}

@keyframes fontColor {
	50% {
		color: coral;
	}

	100% {
		color: black;
	}
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

@keyframes topBounce {

	0%,
	100% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(45px);
	}
}

@keyframes rightBounce {

	0%,
	100% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(45px);
	}
}

@media (max-width: 952px) {
	.effect-1 {
		bottom: 20%;
	}

	.effect-2 {
		bottom: 20%;
	}

	.effect-3 {
		right: 30%;
		height: 150px;
		width: 150px;
	}

	.effect-4 {
		top: 18%;
	}

	.effect-5 {
		top: 15%;
	}
}

@media (max-width: 858px) {
	.effect-1 {
		bottom: 20%;
	}

	.effect-2 {
		bottom: 15%;
	}

	.effect-3 {
		right: 30%;
		height: 150px;
		width: 150px;
	}

	.effect-4 {
		top: 18%;
	}

	.effect-5 {
		top: 15%;
	}
}